/* styles for stream-stuff/components/melee-layout/styles.scss */
$background: rgb(70, 70, 85);
$p1: rgb(250, 90, 90);
$p2: rgb(90, 90, 250);
$p3: rgb(240, 240, 0);
$p4: rgb(110, 200, 110);

._melee-layout_vq8vkj {
  border: 5px solid $background;
  display: flex;

  flex-direction: row;
  height: 1070px;
  width: 1910px;


  ._vidja-game_vq8vkj {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 1313px;

    ._name-space_vq8vkj {
      height: 1030px;
    }

    ._name-plates_vq8vkj {
      background-color: $background;
      display: flex;
      flex-direction: row;
      height: 40px;

      ._name-plate-spacer_vq8vkj {
        width: 50px;
      }

      ._name-wrapper_vq8vkj {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        width: 200px;
        height: 100%;
        padding: 0.1rem;

        &:not(:last-child) {
          margin-right: 110px;
        }

        ._player_vq8vkj {
          font-size: 2rem;
          font-weight: bold;
          margin-right: 1rem;

          &._p1_vq8vkj {
            border-bottom: 2px solid $p1;
            color: $p1;
          }

          &._p2_vq8vkj {
            border-bottom: 2px solid $p2;
            color: $p2;
          }

          &._p3_vq8vkj {
            border-bottom: 2px solid $p3;
            color: $p3;
          }

          &._p4_vq8vkj {
            border-bottom: 2px solid $p4;
            color: $p4;
          }
        }

        ._name_vq8vkj {
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: .25rem;
          color: white;
          font-size: 2.5rem;
          padding: .3rem;
          font-weight: bold;
        }
      }
    }
  }

  ._panel_vq8vkj {
    display: flex;
    flex-direction: column;
    height: 100%;
    flex-grow: 1;
    width: 607px;

    ._vertical-spacer_vq8vkj {
      background-color: $background;
      height: 30px;
    }

    ._web-cam_vq8vkj {
      height: 406px;
      display: flex;
      flex-direction: row;

      ._spacer_vq8vkj {
        background-color: $background;
        width: 34px;
        height: 100%;
      }

      ._camera-box_vq8vkj {
        background: none;
        width: 549px;
      }
    }

    ._the-rest_vq8vkj {
      background-color: $background;
      flex-grow: 1;
    }
  }
}

